<template>
  <div class="ui-aside">
    <div class="logo">
      <h1><img src="./../../assets/image/dashboard.svg"><span></span></h1>
    </div>
    <el-menu
      default-active="/dashboard/editor"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      router>
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span></span>
        </template>
        <el-menu-item index="/dashboard/editor"></el-menu-item>
        <el-menu-item index="/"></el-menu-item>
        <el-menu-item index="/dashboard/index"></el-menu-item>
        <el-submenu index="/dashboard/test">
          <template slot="title">4</template>
          <el-menu-item index="1-4-1">1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="/dashboard/test">
        <i class="el-icon-menu"></i>
        <span slot="title"></span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-setting"></i>
        <span slot="title"></span>
      </el-menu-item>

      <el-submenu index="4">
        <template slot="title">
          <i class="el-icon-setting"></i>
          <span></span>
        </template>
        <el-menu-item index="/dashboard/editor"></el-menu-item>
        <el-menu-item index="/"></el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
  export default {
    methods: {
      handleOpen (key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      }
    }
  }
</script>

<style>
  .ui-aside .logo { 
    height: 60px;
    line-height: 60px;
    width: 220px;
    box-sizing: border-box;
    border-bottom: 1px solid #e6ebf5;
  }
  .ui-aside .logo h1 {
    padding-left: 25px;
    font-weight: 700;
    color: #409eff;
  }
  .ui-aside .logo h1 span {
    display: inline-block;
    vertical-align: middle;
  }
  .ui-aside .logo h1 img {
    margin-right: 10px;
    display: inline-block;
    vertical-align: middle;
    height: 24px;
  }
</style>



