<template>
  <el-container>
    <el-aside width="220px"><ui-aside></ui-aside></el-aside>
    <el-container>
      <el-header><ui-header></ui-header></el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  import uiHeader from './common/Header.vue'
  import uiAside from './common/Aside.vue'

  export default {
    methods: {
      handleOpen (key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      }
    },
    components: {
      uiHeader, uiAside
    }
  }
</script>

<style>
  #app > .el-container {
    height: 100%;
    background-color: #F0F2F5;
  }
  .is-vertical {
    height: 100%;
  }
  .el-header {
    padding: 0;
    background: #fff;
    position: relative;
    z-index: 1;
    border-bottom: 1px solid #e6ebf5;
  }
  .el-aside {
    background-color: #fff;
    position: relative;
    z-index: 2;
    border-right: 1px solid #e6ebf5;
  }
  .el-menu {
    border-right: 0;
  }
</style>




