import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Login from '@/components/Login'
import Register from '@/components/Register'
import Content from '@/components/Content'
import Dashboard from '@/components/Dashboard'
import NotFound from '@/components/NotFound'
import CollectionIndex from '@/components/collection/Index'
import CollectionDebug from '@/components/collection/Debug'
import CollectionWiki from '@/components/collection/Wiki'
import CollectionSetting from '@/components/collection/Setting'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: 'wiki',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/',
      name: 'CollectionIndex',
      component: CollectionIndex
    },
    {
      path: '/content/:id',
      name: 'Content',
      component: Content
    },
    {
      path: '*',
      component: NotFound
    },
    {
      path: '/collection/index',
      name: 'CollectionIndex1',
      component: CollectionIndex
    },
    {
      path: '/collection/debug/:id',
      name: 'CollectionDebug',
      component: CollectionDebug
    },
    {
      path: '/collection/wiki/:id',
      name: 'CollectionWiki',
      component: CollectionWiki
    },
    {
      path: '/collection/setting/:id',
      name: 'CollectionSetting',
      component: CollectionSetting
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'index',
          name: 'DashboardIndex',
          component: resolve => require(['../components/dashboard/Index.vue'], resolve)
        },
        {
          path: 'test',
          name: 'Test',
          component: Index
        },
        {
          path: 'editor',
          name: 'Editor',
          component: resolve => require(['../components/dashboard/Editor.vue'], resolve)
        }
      ]
    }
  ]
})



