<template>
  <div>
    <h2></h2>
    <ul class="ui-nav">
      <li><router-link :to="{ path: 'index' }"></router-link></li>
      <li><router-link :to="{ path: 'login' }"></router-link></li>
      <li><router-link :to="{ path: 'collection/index' }"></router-link></li>
      <li><router-link :to="{ path: 'dashboard/editor' }">Dashboard</router-link></li>
    </ul>
    <div>
      <el-button></el-button>
    </div>
    <div>
      <el-date-picker
        v-model="value3"
        type="datetimerange"
        range-separator=""
        start-placeholder=""
        end-placeholder="">
      </el-date-picker>
    </div>
    <div>
      <el-switch
        v-model="value2"
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch>
    </div>
    <div>
      <el-input
        placeholder=""
        prefix-icon="el-icon-search"
        v-model="input21">
      </el-input>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6"><el-input
        placeholder=""
        prefix-icon="el-icon-search"
        v-model="input21">
      </el-input></el-col>
      </el-row>
    </div>
    <div>
      <el-button :plain="true" @click="open"></el-button>      
      <el-button :plain="true" @click="open2"></el-button>
      <el-button :plain="true" @click="open3"></el-button>
      <el-button :plain="true" @click="open4"></el-button>
    </div>
  </div>
  
</template>
<script>
import Http from '../api/index.js'

export default {
  data () {
    return ({
      input21: '',
      value2: false,
      value3: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 20, 10, 10)]
    })
  },
  created () {
    Http.get('collection/item', null, response => {
      console.log(response)
    })
  },
  methods: {
    open () {
      this.$message('')
    },
    open2 () {
      this.$message({
        message: '',
        type: 'success'
      })
    },
    open3 () {
      this.$message({
        message: '',
        type: 'warning'
      })
    },
    open4 () {
      this.$message.error('')
    }
  }
}
</script>
<style>
  .ui-nav li {
    line-height: 2;
  }
</style>



